import infoText from '../../components/m-info-text/m-info-text'
import infoImg from '../../components/m-info-img/m-info-img'
import setPassword from "../../components/setPassword/setPassword"
import mButton from '../../components/m-button.vue'
import titles from '../../components/title.vue'
import uniIcon from '../../components/uni-icon/uni-icon.vue'
export default {
	data() {
		return {
			titles: "注册",
			text: {
				// user: '角色',
				userName: '姓名',
				userId: '身份证',
				company: '公司',
				phoneNum: '手机',
				city: '所在城市',
				customerName: '客户经理',
				disabled: true,
				// userP: '请选择角色',
				companyP: '选择公司',
				userNameP: '姓名',
				userIdP: '身份证号码',
				phoneNumP: '电话号码',
				cityP: '所在城市',
				customerNameP: '请选择客户经理',
				disabled: true
			},
			pws: {
				textValue: '登录密码',
				newTextValue: '确认密码',
				placeholder: '请输入密码',
				newPlaceholder: '请在此输入密码',
			},
			info: {
				user: '',
				role: '',
				company: '',
				companyId: '',
				userName: '',
				userId: '',
				userPhoneNum: '',
				userCity: '',
				customer: '',
				customerId: '',
				newPwd1: '',
				newPwd2: ''

			},
			btn: {
				type: 'primary',
				value: '下一步',
			},
			items: [{
				id: '1',
				value: '购油人',
				name: '购油人'
			}, {
				id: '2',
				value: '提油人',
				name: '提油人'
			}, {
				id: '3',
				value: '发票领取人',
				name: '发票领取人'
			}],
			showCompany: false,
			showCoutomer: false,
			users: false, //角色选择显示隐藏
			value: "",
			datas: [], //公司集合
			man: [], //客户经理集合
			inputValue: "",
			Cpage: 1,
			page: 1,
			pageSize: 10,
			Cmore: true, //公司查看更多
			more: true, //客户经理查看更多
			checkUser: '',
			showCity: false, //城市页面
			cityDatas: [], //城市集合
			ciytId: '', //城市ID
		}
	},

	methods: {
		//点击角色
		chooseUser() {
			this.users = !this.users;
		},
		userNameBlur(e) {
			console.log(e)
		},

		//选择公司
		oilByCompany() {
			this.getCompanyInfo();
			this.Cmore = true;
			this.datas = [];
			this.showCompany = !this.showCompany;
		},
		// 选中公司
		chooseCompany(e, id) {
			this.info.companyId = id;
			this.info.company = this.datas[e].name;
			this.showCompany = !this.showCompany;

		},
		//公司输入框搜索
		searchCompany() {
			this.Cpage = 1
			if (this.inputValue !== '' && this.inputValue !== null) {
				this.datas = [];
				this.getCompanyInfo()
			} else {
				this.inputValue = '';
				this.datas = [];
				this.getCompanyInfo()
			}
		},
		// 获取公司信息
		getCompanyInfo() {
			this.test.post('base/listCustCompany', {
				search: this.inputValue,
				page: this.Cpage,
				pageSize: this.pageSize
			}).then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					console.log(res)
					res.data.value.forEach(el => {
						this.datas.push(el);
					})
					if (res.data.value.length < 10 && res.data.value.length > 0) {
						this.Cmore = false;
						uni.showToast({
							title: '没有更多了',
							icon: "none"
						})
					}
				}
			}).catch(err => {
				console.log(err)
			})
		},


		//角色选择
		buyAndCarry() {
			// console.log(this.checkUser)
			this.info.user = String(this.checkUser);
			this.users = !this.users;
		},
		checkboxChange(e) {
			// console.log(e)
			this.checkUser = e.target.value;
			var items = this.items,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.includes(item.value)) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
				}
			}
		},


		// 取消
		chooseUsersShow() {
			this.users = !this.users
		},
		//选择市
		getCityInfo() {
			this.test.post('base/listUserCity').then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					res.data.value.forEach(el => {
						this.cityDatas.push(el);
					})
				}
			}).catch(err => {
				console.log(err)
			})
		},
		//选择城市
		chooseCitys() {
			this.cityDatas = [];
			this.showCity = !this.showCity;
			this.getCityInfo();
		},
		chooseCity(index, id) {
			this.showCity = !this.showCity;
			this.info.userCity = this.cityDatas[index].name;
			this.ciytId = id;
			this.page = 1;
			this.test.post('base/listManagers', {
				realname: this.value,
				org_id: this.ciytId,
				size: this.page,
				pageSize: this.pageSize
			}).then(res => {
				console.log(res)
				this.info.customer = res.data.value[0].realname
				this.info.customerId =  res.data.value[0].id
			}).catch(err => {})
		},


		// 选择客户经理
		chooseCustomer(e) {
			this.page = 1;
			this.more = true;
			this.man = [];
			if (this.ciytId !== '') {
				this.getCustomerInfo();
				this.showCoutomer = !this.showCoutomer;
			} else {
				uni.showToast({
					title: '请选择所在城市',
					icon: 'none'
				})
			}
		},
		chooseCustomers(e, id) {
			this.info.customerId = id;
			this.info.customer = this.man[e].realname;
			this.showCoutomer = !this.showCoutomer
		},
		// 搜索客户经理
		searchCustomer() {
			this.page = 1;
			if (this.value !== '' && this.value !== null) {
				this.man = [];
				this.getCustomerInfo();
			} else {
				this.page = 1;
				this.value = '';
				this.man = [];
				this.getCustomerInfo();
			}
		},
		//获取客户经理
		getCustomerInfo() {

			console.log(this.page, this.pageSize)
			this.test.post('base/listManagers', {
				realname: this.value,
				org_id: this.ciytId,
				size: this.page,
				pageSize: this.pageSize
			}).then(res => {
				console.log(res)

				if (res.statusCode == 200 && res.data.errorCode == 0) {
					console.log(res)
					res.data.value.forEach(el => {

						this.man.push(el)
					})


					if (res.data.value.length < 10) {
						this.more = false;
					} else if (res.data.value.length <= 0) {
						this.more = false;
						uni.showToast({
							title: '没有更多了',
							icon: "none"
						})
					}
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 点击加载更多
		Smore() {
			this.page += 1;
			this.getCustomerInfo()
		},
		mores() {
			this.Cpage += 1;
			this.getCompanyInfo()
		},
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		// 下一步
		goPositive() {
			const that = this;
			const _this = this.info;
			console.log(_this.userName)
			if (_this.user !== '' && _this.user !== null) {
				if (_this.company !== '' && _this.company !== null) {
					if (_this.userName !== '' && _this.userName !== null) {
						if (_this.userId !== '' && _this.userId !== null) {
							if (_this.userId.length == 18) {
								if (_this.userPhoneNum !== '' && _this.userPhoneNum !== null) {
									// 判断手机号码
									if (!/^1[3456789]\d{9}$/.test(_this.userPhoneNum)) {
										return uni.showToast({
											"title": '请填写正确的手机号码',
											"icon": "none"
										})
									} else if (_this.userCity !== '' && _this.userCity !== null) {
										if (_this.customer !== '' && _this.customer !== null) {
											if (_this.newPwd1 !== '' && _this.newPwd1 !== null) {
												console.log(_this.newPwd1.length, _this.userId.length)
												if (_this.newPwd1.length >= 6) {
													if (_this.newPwd1 === _this.newPwd2) {
														console.log(this.checkUser)
														if (this.checkUser.length == 1) {
															console.log(_this.user)
															if (this.checkUser[0] == '购油人') {
																this.info.role = '1';
															} else if (this.checkUser[0] == '提油人') {
																this.info.role = '2';
															} else {
																this.info.role = '3';
															}
															console.log(this.info.role)
															uni.setStorage({
																key: 'register',
																data: this.info,
																success: function() {
																	uni.navigateTo({
																		url: "positive/positive1?name=" + "register" + "&user=" + that.checkUser,
																		success: res => {

																		}
																	})
																}
															})

														} else if (this.checkUser.length == 2) {
															console.log(_this.user)
															if (this.checkUser[0] == '购油人' && this.checkUser[1] == '提油人') {
																this.info.role = '1,2';
															} else if (this.checkUser[0] == '购油人' && this.checkUser[1] == '发票领取人') {
																this.info.role = '1,3';
															} else {
																this.info.role = '2,3';
															}
															console.log(this.info.role)
															uni.setStorage({
																key: 'register',
																data: this.info,
																success: function() {
																	uni.navigateTo({
																		url: "positive/positive2?name=" + "register" + "&user=" + that.checkUser,
																		success: res => {

																		}
																	})
																}
															})
														} else if (this.checkUser.length == 3) {
															console.log(_this.user)
															this.info.role = '1,2,3';
															console.log(this.info.role)
															uni.setStorage({
																key: 'register',
																data: this.info,
																success: function() {
																	uni.navigateTo({
																		url: "positive/positive?name=" + "register" + "&user=" + that.checkUser,
																		success: res => {

																		}
																	})
																}
															})
														}


														// 判断密码是否一致	
													} else {
														return uni.showToast({
															title: '两次密码不一致',
															icon: 'none'
														})
													}
												} else {
													return uni.showToast({
														title: '密码最小不能少于6位',
														icon: 'none'
													})
												}
												// 判断密码	
											} else {
												return uni.showToast({
													title: '密码不能为空',
													icon: 'none'
												})
											}
											// 判断是否选择客户经理
										} else {
											return uni.showToast({
												title: '请选择客户经理',
												icon: 'none'
											})
										}
										// 判断城市是否填写	
									} else {
										return uni.showToast({
											title: '所在城市不能为空',
											icon: 'none'
										})
									}
								} else {
									return uni.showToast({
										title: '手机号码不能为空',
										icon: 'none'
									})
								}
								//判断身份证号码位数是否18位
							} else {
								return uni.showToast({
									title: '请填写18位身份证号码',
									icon: 'none'
								})
							}
							//判断身份证号码
						} else {
							return uni.showToast({
								title: '身份证号码不能为空',
								icon: 'none'
							})
						}
						//判断姓名是否填写
					} else {
						return uni.showToast({
							title: '姓名不能为空',
							icon: 'none'
						})
					}
					//判断公司是否选择
				} else {
					return uni.showToast({
						title: '请选择公司',
						icon: 'none'
					})
				}
				//判断角色是否选择
			} else {
				return uni.showToast({
					title: '请选择角色',
					icon: 'none'
				})
			}




		},

	},
	components: {
		infoText,
		infoImg,
		setPassword,
		mButton,
		uniIcon,
		titles,

	},

}
